import React, { ReactNode, Ref } from 'react';
import {  useIntl} from 'react-intl';





// material-ui
import { useTheme } from '@mui/material/styles';
import { Card, CardContent, CardHeader, Divider, Typography } from '@mui/material';

export interface SubCardProps {
    children: ReactNode | string | null;
    content?: boolean;
    className?: string;
    contentClass?: string;
    // darkTitle?: boolean;
    secondary?: ReactNode | string | {};
    sx?: {};
    contentSX?: {};
    title?: ReactNode | string | {};
}

// ==============================|| CUSTOM SUB CARD ||============================== //

const SubCard = React.forwardRef(
    (
        { children, className, content, contentClass,  secondary, sx = {}, contentSX = {}, title, ...others }: SubCardProps,
        ref: Ref<HTMLDivElement>
    ) => {
        const theme = useTheme();
        try{

            if(typeof title==='string')
                title = useIntl().formatMessage({id:title});
                
        }
        catch(error){
            console.log(title, error);
            
        }
       
        return (
            <Card
            
                ref={ref}
                
                sx={{
                    
                    border: '1px solid',
                    alignContent:'center',
                    margin:'1rem',
          
            
                    borderColor: theme.palette.mode === 'dark' ? theme.palette.dark.light + 15 : theme.palette.primary.light,
                    ':hover': {
                        boxShadow: theme.palette.mode === 'dark' ? '0 2px 14px 0 rgb(33 150 243 / 10%)' : '0 2px 14px 0 rgb(32 40 45 / 8%)'
                    },
                    
                    ...sx
                
                }}
                {...others}
            >
                {/* card header and action */}
                {title && (
                    <CardHeader sx={{ p: 2.5, textAlign:'center',...contentSX }} title={<Typography variant="h5">{title}</Typography>} action={secondary} />
                )}
                {/* {darkTitle && title && (
                    <CardHeader sx={{ p: 2.5 , textAlign:'center',...contentSX }} title={<Typography variant="h4">{title}</Typography>} action={secondary} />
                )} */}
{/* sx={{textAlign:'center'}} */}
                {/* content & header divider */}
                {title && (
                    <Divider
                        sx={{
                            opacity: 1,
                            borderColor: theme.palette.mode === 'dark' ? theme.palette.dark.light + 15 : theme.palette.primary.light
                        }}
                    />
                )}

                {/* card content */}
                {content && (
                    <CardContent  sx={{ 
                        
                        p: 2.5, ...contentSX }} className={contentClass || ''}>
                        {children}
                    </CardContent>
                )}
                {!content && children}
            </Card>
        );
    }
);

SubCard.defaultProps = {
    content: true
};

export default SubCard;
