import   { useEffect, useState } from 'react';
import { useIntl } from 'react-intl';
import ReactLoading from 'react-loading';
import  { Moment } from 'moment';
import SubCard from 'ui-component/cards/SubCard';
import AttackDash from '../../secunity-ui/component/attack_info/AttackDash';
import { Grid } from '@mui/material';
import { useDispatch, useSelector } from 'react-redux';

import ApexLineChart from 'views/secunity-ui/Apexchart/ApexLineChart';

import { DefaultRootStateProps, NavigationEnum, Model, Headers_Enum, ATTACK_FIELDS, DashboardTabEnum } from 'types';
import { change_time_for_time_zone, create_time_arr_moment } from 'types/mock/traffic';
import {
    // add_compere_for_sort,
    pydict,
    calculate_chart,
    get_table_data,
    generic_click_util,
    generic_handleSelectAllClick_util,
    // get_attacks
} from 'utils';
import {
    ADD_MANY_SIGNED1,
    // ADD_MANY_SIGNED1,
    SET_SELECTED
} from 'store/state/type_actions';
import { useParams } from 'react-router-dom';

import useAuth from 'common/hooks/useAuth';
import TableDashAttack from './TableDashAttack';
import TableDashRows from './TableDashRows';
import style from './style_main_dashborad';
import { Dashborad } from 'types/dashboard/Dashboard';

// todo work on calculate chart and on the chossing but before change the name in the context

const ManageDash = ({ model_name }: Model) => {
    const { model, cells, headCells } = get_table_data(model_name);
    const params = useParams();
    const { id } = params;
    const { permissions } = useAuth();

    const dispatch = useDispatch();

    const dashboard_data = useSelector((state: DefaultRootStateProps) => state.dashboard[model_name]);
    const context = useSelector((state: DefaultRootStateProps) => state.context[model_name]);

    const dashboard_class: Dashborad = pydict(dashboard_data, `dashboard_class`, false);

    const add_leading_row = (row: any) => {
        pydict(row, dashboard_class.attack_table_key, []).length > 0 &&
            dispatch({
                type: ADD_MANY_SIGNED1,
                model,
                signed_key: dashboard_class.attack_table_key,
                obj: row[dashboard_class.attack_table_key]
            });
    };

    const generic_click = (name: string, type: DashboardTabEnum,singed:boolean) => {
        const obj = generic_click_util(name, type, singed, dashboard_class, context);
        dispatch({ type: SET_SELECTED, model, obj });
    };

 
    const handleSelectAllClick = (checked:boolean, type: DashboardTabEnum,singed:boolean) => {
        const obj = generic_handleSelectAllClick_util(checked, type,singed, dashboard_class, context);
        
        dispatch({ type: SET_SELECTED, model, obj });
    };
 
 
    const loading = <ReactLoading type="cubes" />;
    const [table, setTable] = useState<JSX.Element>(loading);
    const [tableAttack, setTableAttack] = useState<JSX.Element>(loading);
    const [chart, setChart] = useState<JSX.Element>(loading);

    const global_attack = useIntl().formatMessage({ id: Headers_Enum.GLOBAL_ATTACKS });
    const chart_line_name = useIntl().formatMessage({
        id: model !== NavigationEnum.NETWORKS_TABLE ? Headers_Enum.DEVICES : Headers_Enum.TRAFFIC
    });

    useEffect(() => {
        
        if (dashboard_class) {
            setTableAttack(
                <TableDashAttack
                    model_name={model_name}
                    handleSelectAllClick={handleSelectAllClick}
                    generic_click={generic_click}
                    context={context}
                    model={model}
                    dashboard_data={dashboard_data}
                />
            );

            setTable(
                <TableDashRows
                    add_leading_row={add_leading_row}
                    dashboard_data={dashboard_data}
                    model_name={model_name}
                    generic_click={generic_click}
                    headCells={headCells}
                    context={context}
                    handleSelectAllClick={handleSelectAllClick}

                    model={model}
                    cells={cells}
                    permissions={permissions}
                />
            );
            let { sa, sb, indexs_to_show } = calculate_chart(dashboard_class, model, context, dashboard_data, chart_line_name, global_attack);
            const moment_lead_time: Moment = change_time_for_time_zone(dashboard_class.totals.max_time);
          
            const array_times = create_time_arr_moment(moment_lead_time);
                
            if (!permissions.has_client_flowspec_device && model === NavigationEnum.ATTACKS_TABLE) {
                sa = sa.filter((line: any) => !line.name.includes('FLOWS') && chart_line_name !== line.name);
                sb = sb.filter((line: any) => !line.name.includes('FLOWS') && chart_line_name !== line.name);
                indexs_to_show=[]
            }
            setChart(
                <SubCard {...style.chart_card}>
                    <SubCard title={ATTACK_FIELDS.TRAFFIC_BPS} {...style.chart_card}>
                        <ApexLineChart indexs_to_show={indexs_to_show} series_data={sa} bps xaxis_categories={array_times} />
                    </SubCard>
                    <SubCard   title={ATTACK_FIELDS.TRAFFIC_PPS} {...style.chart_card}>
                        <ApexLineChart  indexs_to_show={indexs_to_show} series_data={sb} xaxis_categories={array_times} />
                    </SubCard>
                </SubCard>
            );
        } else {
            setTable(loading);
            setTableAttack(loading);
            setChart(loading);
        }
    }, [model_name, dashboard_data, context  ]);
    
    const down_attack_detail = <AttackDash id={id} model_name={model} />;
    const ans: any[] = [
        table,
        <Grid {...style.chart_grid}>
            {chart}
            {down_attack_detail}
        </Grid>,
        tableAttack
    ];

    return <Grid {...style.main_grid}>{model !== NavigationEnum.ATTACKS_TABLE ? ans : ans.reverse()}</Grid>;
};
export default ManageDash;
