import { Grid,  Table,  TableBody, TableContainer } from '@mui/material';

import MainCard from 'ui-component/cards/MainCard';
import { EnhancedTableHead } from '../table_model/TableHeader';

import {  HeadCell, headCellsReport, NavigationEnum, report_fields } from 'types';

import useAuth from 'common/hooks/useAuth';

import RowTable from './RowTable';
import HocNavigate from 'common/hooks/useNavigate';
import React from 'react';


interface Props {
    cells: string[];
    rows: any[];
    model: NavigationEnum;
    headCells: HeadCell[];
}
const TableList = ({ cells, model, headCells, rows }: Props) => {
 
    return (
        <MainCard headerSX={{ textAlign: 'center' }} title={model.toUpperCase().split('_').join(' ')} content={false}>
    
            <TableContainer>
                <Table sx={{ minWidth: 750 }}>
                    <EnhancedTableHead
                        headers={headCells}
                        // numSelected={selected.length}
                        order='asc'
                        orderBy='sort'
                        rowCount={rows ? rows.length : 0}
                    />
                    <TableBody>
                        {
                            rows &&
                                rows.map((row, index) => {
                                    if (typeof row === 'number') return null;

                                    return (
                                        <RowTable
                                            cells={cells}
                                            model={model}
                                            row_obj={row}
                                            index={index}
                                        />
                                    );
                                })
                        }
                    </TableBody>
                </Table>
            </TableContainer>
 
        </MainCard>
    );
};

const ReportTableMain = () => {
 
    const [rows, setRows] = React.useState<any[]>([]);
    const { permissions } = useAuth();

    const [cells, headCells] =[report_fields, headCellsReport]
    const navigate = HocNavigate();
  
    const fetch_data = async ( ) => {
        const ans = await navigate.get_report()
        
        if(ans[0] && permissions.accounts){
            const rows_with_account = ans[1].map((row:any)=> {return{...row, account: permissions.accounts[row['account']]} })
        setRows(rows_with_account)
 }
   
    };

    React.useEffect(()=>{
        permissions && fetch_data()

    },[permissions])
    
    return (
        <Grid  alignContent='right' justifyItems='right' justifyContent='right'>
                    <MainCard>
            <TableList
            cells={cells}
            rows={rows}

            model={NavigationEnum.REPORTS}
            headCells={headCells}
        />
                    </MainCard>
             
           
        </Grid>
 
    );
};

export default ReportTableMain;
