import { useSelector } from 'react-redux';
import AttackCard from './AttackCard';

import { ATTACK_FIELDS, DefaultRootStateProps, NavigationEnum } from 'types';
import { pydict } from 'utils/transformation/pydict';
import { array_obj_to_obj } from 'utils';
import { Attack } from 'types/dashboard';
import { Dashborad } from 'types/dashboard/Dashboard';


const AttackDash = ({ model_name, id }: any) => {
    const dashboard_class: Dashborad = pydict(
        useSelector((state: DefaultRootStateProps) => state.dashboard),
        `${model_name}.dashboard_class`,
        false
    );

    if (id !== undefined && dashboard_class) {
        const [device, attack] = [
            dashboard_class.devices.get_merege_model(),
            pydict(dashboard_class.merege_attacks(), `${id}.flow`, { flows: {} }),
        ];

        const lead_time = dashboard_class.totals.max_time

        const flows: Attack[] = Object.keys(pydict(attack, NavigationEnum.FLOWS_TABLE, {}))
            .map((key: string) =>  dashboard_class.flows[key])
            .map((att: any) => {
                return {
                    ...att.flow.destination,
                    device: att.flow.device
                        ? att.flow.device
                        : array_obj_to_obj(device, att.flow.account_network_device) &&
                          array_obj_to_obj(device, att.flow.account_network_device).client_flowspec,
                    id: att.flow.id,

                    stat: {
                        bps: pydict(att, `${ATTACK_FIELDS.STATICTICS}.${lead_time}.${ATTACK_FIELDS.BPS}`, 0),
                        pps: pydict(att, `${ATTACK_FIELDS.STATICTICS}.${lead_time}.${ATTACK_FIELDS.PPS}`, 0)
                    }
                };
            });

        return <AttackCard attacks={flows} incoming_flow={attack} />;
    }
    return null;
};
export default AttackDash;

