import { useEffect, useState } from 'react';
import {
    // useTheme,
    styled
} from '@mui/material/styles';

// material-ui
import { Grid, Typography } from '@mui/material';
import MainCard from 'ui-component/cards/MainCard';
import SubCard from 'ui-component/cards/SubCard';

// import CardInfo from './CardInfo';
// import Gauge from './Gauge';
import CardAttack from './CardAttack';
import GaugeChart from 'react-gauge-chart';
import { DashboardTabEnum, DefaultRootStateProps, Model } from 'types';
import { useSelector } from 'react-redux';
import { pydict } from 'utils';
import { FormattedMessage } from 'react-intl';
import useAuth from 'common/hooks/useAuth';
// import { make_total_with_totals_api, default_totals } from 'utils/transformation/gauge_info';
// import { Dashborad } from 'types/dashboard/Dashboard';
import { DashboradTotals } from 'utils/transformation/gauge_info';
import ReactLoading from "react-loading";

// todo make util function for the reduce calulation

const FooterSubWrapper = styled('div')(({ theme }) => ({
    [theme.breakpoints.up(1700)]: {
        marginLeft: '-1.81rem'
    }
}));

const Dashboard = ({ model_name }: Model) => {
    const dashboard_data = useSelector((state: DefaultRootStateProps) => state.dashboard[model_name as 'networks']);
    const totals: DashboradTotals = pydict(dashboard_data, `dashboard_class.totals`, false);

    // const context = useSelector((state: DefaultRootStateProps) => state.context[model_name as 'networks']);
    const { permissions } = useAuth();
    const style:any = {
        sub_card_info :{ contentSX:{   m: -2.5,   p: -2.5,   '&:hover .tooltip': {       zIndex: 1,       color: 'black'   }}},
        grid_typography: { item: true, display: "flex", direction: "row", justifyContent: "center",},
        typography_gauge: { justifyContent: "center",sx:{ zIndex: -2010, color: '#fff', mt: '-0.99rem', textAlign: 'center', alignItems: 'center', alignContent: 'center' }},
        main_grid: { container: true,  xs: 12,  justifyContent:'center'},
        table_attack_grid: { item: true,  xs: 12,  md: 4, lg: 3.5, xl: 2.1,},
        table_model_grid: { item: true,  xs: 12,  md: 4, lg: 3.5, xl: 1.65},
    }

    const [gauge_bps_pps, setgauge_bps_pps] = useState<JSX.Element[]>(Array(2).fill(<ReactLoading  type='cubes' color='primary'/>));
    // const [max_time, setMax_time] = useState<string>('');
    useEffect(() => {
       
        if(totals  ){
        const gauge_bps_pps_to_set = totals.get_total_for_info( model_name).map((total: any) => {
            return (
                <Grid lg={5} item>
                    <SubCard
                        title={total.title}
                        {...style.sub_card_info}
                     
                    >
                        {/* <Gauge/> */}
                        <Grid {...style.grid_typography}  >
                            <Typography
                            {...style.typography_gauge}
                            >
                                <FormattedMessage id={total.text} />
                            </Typography>
                        </Grid>
                        <Grid item display="flex" direction="row" justifyContent="space-between">
                            <Grid item lg={3} md={6} sm={6} xs={12} ml={4}>
                                <CardAttack
                                    is_attack_or_info={false}
                                    primary_text={total.bps.value.toString()}
                                    secondary_text={total.bps.text}
                                />
                            </Grid>

                            <Grid item lg={6} md={6} sm={6} xs={12} ml={1} mt={-1} justifyContent="center">
                                <GaugeChart
                                    id="gauge-chart4"
                                    textColor="black"
                                    style={{
                                        width: '13rem',
                                        height: '5rem',
                                        // marginLeft:'1.1rem',
                                        alignItems: 'center',
                                        alignContent: 'center'
                                    }}
                                    arcsLength={[0.4, 0.4, 0.2]}
                                    nrOfLevels={10}
                                    arcPadding={0.1}
                                    cornerRadius={3}
                                    percent={total.gauge}
                                />
                                <FooterSubWrapper>
                                    <Typography
                                        variant="h4"
                                        sx={{
                                            zIndex: 2010,
                                            textAlign: 'center',
                                            alignItems: 'center',
                                            alignContent: 'center'
                                        }}
                                    >
                                        of accounts bandwidth
                                    </Typography>
                                </FooterSubWrapper>
                            </Grid>
                            <Grid item lg={3} md={6} sm={6} xs={12} ml={4}>
                                <CardAttack is_attack_or_info={false} primary_text={total.pps.toString()} secondary_text="PPS" />
                            </Grid>
                        </Grid>
                    </SubCard>
                </Grid>
            );
        });
        // setMax_time(totals.max_time)
        setgauge_bps_pps(gauge_bps_pps_to_set);}
    }, [totals, model_name]);

    return (
        <MainCard sx={{ width: 0.98, ml: 1, mt: -1 }}>
            <Grid
                container
                mt={-3}
                mb={-3}
                lg={12}
                xs={12}
                spacing={0}
                marginTop={-4}
                marginBottom={-4}
                paddingTop={-4}
                display="flex"
                direction="row"
                justifyContent={
                    model_name !== DashboardTabEnum.ATTACKS_TABLE || permissions.has_client_flowspec_device ? 'space-between' : 'center'
                }
            >
                {gauge_bps_pps[0]}

                <Grid item lg={1} md={6} sm={6} xs={12} mt={5}>
                    <CardAttack is_attack_or_info primary_text={`${totals && totals.under_attack ? ' ' : 'not '}under attack`} />
                </Grid>
                {model_name !== DashboardTabEnum.ATTACKS_TABLE || permissions.has_client_flowspec_device ? gauge_bps_pps[1] : null}
            </Grid>
        </MainCard>
    );
};

export default Dashboard;
