 
import { Grid } from '@mui/material';

import TableDash, { TableDashProps } from 'views/secunity-ui/Table/table_dash/TableDash';
import {
    headCellsAttack_dashboard,
    NavigationEnum,
    attack_dash,
} from 'types';
import {    pydict,} from 'utils';
import {
    get_attacks,
} from '../../../utils/dashboard/help_function';
import style from './style_main_dashborad';

import { Dashborad } from 'types/dashboard/Dashboard';

const TableDashAttack = ({ dashboard_data, model_name, generic_click, context, model,    handleSelectAllClick }: any) => {
    
    const dashboard_class :Dashborad= pydict(dashboard_data, `dashboard_class`, {})
    const signed_key = dashboard_class.attack_table_key
    
    

    const props:TableDashProps = {
        model: model_name,
        checkbox: true,
        selected:[],
        handleClick: generic_click,
        cells: attack_dash,
        headCells: headCellsAttack_dashboard,
        signed_key,
        model_name: NavigationEnum.ATTACKS_TABLE,
        rows: get_attacks(dashboard_class.get_signed_objs_attacks())
    };
    return (
        <Grid {...style.table_attack_grid}>
            {dashboard_class.show_signed_attacks() && <TableDash {...props} selected={context.attacks}
                handleSelectAllClick={handleSelectAllClick}
            
            />}

            <TableDash
                handleSelectAllClick={handleSelectAllClick}
                {...props}
                selected={context.attacks}
                rows={get_attacks(dashboard_class.get_objs_attacks())}
                signed_ids={new Set(Object.keys(dashboard_class.get_signed_objs_attacks()))}
                total={dashboard_class.get_total_attacks()}
            />
{Object.keys(dashboard_class.holistic.get_signed_objs()).length!==0&&
            <TableDash
                handleSelectAllClick={handleSelectAllClick}
                {...props}
                selected={context.holistic}
                signed_key={ NavigationEnum.HOLISTIC}
                model_name={ NavigationEnum.HOLISTIC}
                rows={get_attacks(dashboard_class.holistic.get_signed_objs())}
                // total={dashboard_class.holistic.get_total()}
            />}
{Object.keys(dashboard_class.holistic.get_objs()).length!==0&&
            <TableDash
                handleSelectAllClick={handleSelectAllClick}
                {...props}
                selected={context.holistic}
                signed_ids={new Set(Object.keys(dashboard_class.holistic.get_signed_objs()))}
                signed_key={ NavigationEnum.HOLISTIC}
                model_name={ NavigationEnum.HOLISTIC}
                rows={get_attacks(dashboard_class.holistic.get_objs())}
                total={dashboard_class.holistic.get_total()}
            />}
        </Grid>
    );
};

export default TableDashAttack