
 
import { Grid,  Divider } from '@mui/material';

import TableDash from 'views/secunity-ui/Table/table_dash/TableDash';
import {
    headCellsNetwork_dashboard,
    NavigationEnum,
    network_dash,
} from 'types';
import {
 
    pydict,
    array_objs_array_value,
    dict_to_array,
} from 'utils';
import style from './style_main_dashborad';
import { Dashborad } from 'types/dashboard/Dashboard';
 

const TableDashRows = ({
    dashboard_data,
    model_name,
    generic_click,
    headCells,
    context,
    model,
    // rows,
    cells,
    permissions,
    add_leading_row,
    handleSelectAllClick
}: any) => {

    const dashboard_class :Dashborad= pydict(dashboard_data, `dashboard_class`, {})
    const signed_key = dashboard_class.model_table_key
    
    


    const selected =  context[signed_key];
    const props = {
        model: model_name,
        checkbox: true,
        handleClick: generic_click,
        selected,
        cells,
        func_add: add_leading_row,
        handleSelectAllClick,

        headCells,
        signed_key,
        model_name: signed_key
    };

    return (
        <Grid  {...style.table_model_grid}>
            {dashboard_class.show_signed_model() && <TableDash {...props} rows={dict_to_array(dashboard_class.get_signed_objs_model())} headCells={headCells} />}
            {dashboard_class.show_signed_model()  && <Divider sx={{ my: 1.5 }} />}

            {(permissions.has_client_flowspec_device || signed_key !== NavigationEnum.DEVICES_TABLE) && (
                <TableDash
                    signed_ids={new Set(array_objs_array_value(dashboard_class.get_signed_objs_model(), 'id'))}
                    rows={dict_to_array(dashboard_class.get_objs_model())}
                    {...props}
                    total={dashboard_class.get_total_model()}
                />
            )}

            {model === NavigationEnum.ATTACKS_TABLE && dashboard_class.networks.get_total() > 0  && (
                <TableDash
                    cells={network_dash}
                    rows={dict_to_array(dashboard_class.networks.get_objs())}
                    headCells={headCellsNetwork_dashboard}
                    model_name={NavigationEnum.NETWORKS_TABLE}
                    model={ model_name}
                signed_ids={false}

                    selected={[]}
                    checkbox={false}
                />
            )}
            {dashboard_class.blackholes.get_total() > 0 && (
                <TableDash
                    {...props}
                    rows={dict_to_array(dashboard_class.blackholes.get_objs())}
                    model={model_name}
                    selected={[]}
                signed_ids={false}

                    checkbox={false}
                    cells={['cidr']}
                    headCells={headCellsNetwork_dashboard}
                    model_name={NavigationEnum.BLACKHOLE}
                />
            )}
        </Grid>
    );
};
export default TableDashRows
