import { Dispatch } from 'redux';
import { SET_MODELS } from 'store/state/type_actions';
import { FLOW_ACTION } from 'types';
import { objApi } from 'utils/api';
// import { get_table_data } from 'views/secunity-ui/Table';

export const get_model_by_id = (model: string, id: string) => async (dispatch: Dispatch) => {
    await objApi.get(`/${model}/${id}`, dispatch);
};

export const submit_form = (model: string, obj_id: string, obj_to_send: any) => async (dispatch: Dispatch) => {
    
    const edit: boolean = typeof obj_id === 'string' && !['', 'add'].includes(obj_id);
    const ans =await objApi.post(`/${model}/${edit ? `${obj_id}/edit` : 'add'}`, obj_to_send, dispatch);
    
    return ans
};
export const delete_item = (model: string, obj_id: string ) => async (dispatch: any) => {
    // const edit: boolean = typeof obj_id === 'string' && !['', 'add'].includes(obj_id);
    const ans:any = await objApi.delete(`/${model}/${obj_id}/delete`, dispatch);


    return ans[0];
};
export const flow_action = (model: string, obj_id: string,action:FLOW_ACTION, model_name:string) => async (dispatch: any) => {
    // const ans:any = await objApi.post(`/outgoing/action/${obj_id}/${action}`,{}, dispatch);
    const ans:any = await objApi.post(`/${model}/action/${obj_id}/${action}`,{}, dispatch);
    if(ans[0]){
        const model_flow = `${model.split('_')[0]}_${model_name}`
        await  dispatch(set_models(model_flow, { options: ["extended",'general',] }));

    }

    return ans[0];
};




export const set_models = (model: string, body:any) => async (dispatch:any)  => {
    const url = ['active', 'cooldown'].some(word => model.includes(word))?`${model.split('_')[0]}/attacks/${model.split('_')[1]}`:model;
    

        const [success, obj] =await objApi.post(`/${url}`, body, dispatch);
        model = model.split('_')[1]
        if(success)
        dispatch({
            type: SET_MODELS,
            [model]: obj,
        }) 

  };


  