import { useEffect, useState } from 'react';

import {
    CardContent,
    Divider,
    Grid,
    Typography,
    // Checkbox,
    IconButton,
    // ListItemButton,
    Chip,
    Button
} from '@mui/material';
// import { useDispatch } from 'react-redux';

// project imports
import SubCard from 'ui-component/cards/SubCard';
import { gridSpacing } from 'store/template/constant';
import DashboardIcon from '@mui/icons-material/Dashboard';
import { FormattedMessage } from 'react-intl';
import HocNavigate from 'common/hooks/useNavigate';

import { Attack } from 'types/dashboard';
// import { SET_SELECTED } from 'store/state/type_actions';
import DisabledByDefaultIcon from '@mui/icons-material/DisabledByDefault';
import { ACCOUNT_TYPE, ATTACK_FIELDS, NavigationEnum } from 'types';
import { normliaze_bps, pydict, normliaze_pps } from 'utils';
import useAuth from 'common/hooks/useAuth';
import { Language } from 'types';

export interface PopularCardProps {
    attacks: Attack[];
    incoming_flow: any;
}

const AttackCard = ({ attacks, incoming_flow }: PopularCardProps) => {
    const navigate = HocNavigate();
    const { model_name } = navigate.params;
    
    const { permissions } = useAuth();

    const [devices_flows, set_devices_flows] = useState<any>(null);
    const style: any = {
        grid_devices_flows: { item: true, lg: 5.3, m: 1, mt: 0, borderRadius: 3, border: '1px dashed', direction: 'row' },
        sub_grid_devices_flows: { item: true, direction: 'row' },
        grid_checkbox_devices_flows: { item: true, display: 'flex', justifyContent: 'center', alignItems: 'center' },
        grid_value_num: { item: true, display: 'flex', justifyContent: 'space-between', direction: 'row' },
        grid_inner_value_num: { alignItems: 'space-between', p: 1, display: 'flex', direction: 'column' },
        typography_value_num: { variant: 'subtitle1', color: 'inherit', textAlign: 'center' },
        grid_device: { item: true, display: 'flex', alignItems: 'center', direction: 'column', mb: 2 },
        typography_device: { variant: 'h4', color: 'primary.dark', textAlign: 'center' }
    };
    useEffect(() => {
        set_devices_flows(
            attacks.map((attack: Attack) => (
                <Grid {...style.grid_devices_flows}>
                    <Grid {...style.sub_grid_devices_flows}>
                        <Grid {...style.grid_value_num}>
                            {Object.keys(attack.stat).map((key: string) => {
                                const { value, text } = key === 'bps' ? normliaze_bps(attack.stat[key]) : normliaze_pps(attack.stat[key]);
                                return (
                                    <Grid {...style.grid_inner_value_num}>
                                        <Typography {...style.typography_value_num}>
                                            {text.length > 0 ? `${value}|${text}` : value}
                                        </Typography>
                                        <Typography {...style.typography_value_num}>{key}</Typography>
                                    </Grid>
                                );
                            })}
                        </Grid>
                    </Grid>
                    <Grid {...style.grid_device}>
                        <Typography {...style.typography_device}>{attack.device}</Typography>
                        <Typography {...style.typography_device}>
                            <FormattedMessage id="device" />
                        </Typography>
                    </Grid>
                </Grid>
            ))
        );
    }, [attacks, incoming_flow]);

    const navigate_to_attacks = () => {
        const value = incoming_flow.id;
        // const value   =  attack['incoming_flow']
        localStorage.setItem('dashboard_tab', '2');

        navigate.request_navigate(`dashboard/attacks/${value}`);
    };

    const style_icon_close: any = {
        grid: { display: 'flex', direction: 'row' }
    };
    const icon_close = (
        <Grid {...style_icon_close.grid}>
            {[ACCOUNT_TYPE.ISP, ACCOUNT_TYPE.ROOT].includes(permissions.type_account) && (
                <Typography color="secondary" mt={1.5} mr={2}>
                    {permissions.accounts[incoming_flow.account]}
                    <Divider orientation="vertical" flexItem />
                </Typography>
            )}
            {model_name !== NavigationEnum.ATTACKS_TABLE && (
                <Button sx={{ mr: 12 }} autoFocus onClick={navigate_to_attacks} endIcon={<DashboardIcon />}>
                    <FormattedMessage id={Language.SHOW_IN_ATTACKS} />
                </Button>
            )}

            <Divider orientation="vertical" flexItem />

            <IconButton
                color="primary"
                size="large"
                onClick={() => {
                    navigate.request_navigate(`dashboard/${model_name}`);
                }}
            >
                <DisabledByDefaultIcon sx={{ fontSize: '1.3rem' }} />
            </IconButton>
        </Grid>
    );
    const style_card: any = {
        main_card: {
            contentSX: { m: -2.5, p: -1.5 },
            sx: { backgroundColor: 'secondary.light', ml: '2%', width: '96%', position: 'sticky', height: 'auto', bottom: 0, zIndex: 11 }
        },
        grid_content: { item: true, lg: 12, display: 'flex', direction: 'row', spacing: gridSpacing },
        grid_devices_flows: { container: true, lg: 4, xs: 6, display: 'flex', direction: 'row', sx: { justifyContent: 'space-between' } },
        divider: { flexItem: true, orientation: 'vertical', sx: { borderRightWidth: 3, borderColor: 'primary.dark' } },
        grid_src_dst: { item: true, lg: 6, xs: 12, ml: 2, display: 'flex', direction: 'row' },
        grid_src_dst_header: { item: true, mt: 3, lg: 2, display: 'flex', direction: 'column' },
        grid_src_dst_typography_header: { mt: 1, variant: 'body1', textAlign: 'center' },
        grid_src_dst_body: { item: true, lg: 4, xs: 12, display: 'flex', direction: 'column' },
        grid_src_dst_typography_body: { mt: 1, minWidth: '5rem',  },
        grid_other: { container: true, lg: 2, ml: 1, display: 'flex', direction: 'column' },
        grid_other_field: { item: true,  mb: 1, display: 'flex', direction: 'row' ,justifyContent: 'space-between'},
    };

    return (
        <SubCard title="ATTACK DETAILS" secondary={icon_close} {...style_card.main_card}>
            <CardContent>
                <Grid {...style_card.grid_content}>
                    <Grid {...style_card.grid_devices_flows}>{devices_flows}</Grid>
                    <Divider {...style_card.divider} />
                    <Grid {...style_card.grid_src_dst}>
                        <Grid {...style_card.grid_src_dst_header}>
                            {[ATTACK_FIELDS.IP, ATTACK_FIELDS.MASK, ATTACK_FIELDS.PORTS, ].map((field: ATTACK_FIELDS) => (
                                <Typography {...style_card.grid_src_dst_typography_header}>{field}</Typography>
                            ))}
                        </Grid>

                        {[ATTACK_FIELDS.DESTINATION, ATTACK_FIELDS.SOURCE].map((key: string) => (
                            <Grid {...style_card.grid_src_dst_body}>
                                <Typography variant="button">
                            <FormattedMessage id={key} />
                                   </Typography>

                                {[ATTACK_FIELDS.IP, ATTACK_FIELDS.MASK, ATTACK_FIELDS.PORTS, ].map((key2: string) => (
                                <Typography {...style_card.grid_src_dst_typography_body}>
                                    {`${pydict(incoming_flow,`${key}.${key2}`,'--').toString()}`}</Typography>
                                    ))}
                            </Grid>
                        ))}
                        <Divider {...style_card.divider} />

                        <Grid {...style_card.grid_other}>

                            {[ ATTACK_FIELDS.PROTOCOL ,ATTACK_FIELDS.PACKET_LENGTH, ATTACK_FIELDS.TCP_FLAGS, ATTACK_FIELDS.FRAGMENT , ATTACK_FIELDS.ICMP_CODE , ATTACK_FIELDS.ICMP_TYPE ].filter((field:ATTACK_FIELDS)=> field in incoming_flow)
                            .map((field:ATTACK_FIELDS)=>
                <Grid {...style_card.grid_other_field}>
                            
                            <Typography minWidth="6rem" variant="button" >
                            <FormattedMessage id={field} />

                            </Typography>
                            {
                               field===ATTACK_FIELDS.TCP_FLAGS ? pydict(incoming_flow, field, []).map((flag: string) => (
                                    <Chip
                                        label={flag}
                                        size="small"
                                        color="primary"
                                        sx={{ mb: 1 }}
                                    />
                                )):        <Typography minWidth="5rem"  textAlign="center">
                                {pydict(incoming_flow, field, '')}
                            </Typography>
                            }
                    
                        </Grid>

                            )}
                  
                    </Grid>
                    </Grid>
                </Grid>
            </CardContent>
        </SubCard>
    );
};

export default AttackCard;
