import { Dispatch } from "redux";
import { SET_DASHBOARD,
  //  SET_SELECTED
   } from "store/state/type_actions";
import { NavigationEnum } from "types";
import { pydict } from "utils";
import { objApi } from "utils/api";

export const transfom_to_signed =(obj:any, model:string)=>{
  switch (model) {
    case NavigationEnum.NETWORKS_TABLE:
        return {
          signed_right: pydict(obj,'attacks.signed_objs',{}),
          signed_left: pydict(obj,'networks.signed_objs',[])}
          
    case NavigationEnum.DEVICES_TABLE:
      return {
        signed_right: pydict(obj,'flows.signed_objs',{}),
      signed_left: pydict(obj,'devices.signed_objs',[])}
      
    default:
      return {
        signed_right: pydict(obj,'devices.signed_objs',[]),
      signed_left: pydict(obj,'attacks.signed_objs',{})
    }
      
}

}



export const set_dashbord = (model: string, body={}) => async (dispatch:Dispatch)  => {
  try {
    const model_id= model.split('_')
    model = model_id[1]
    
    const [success, obj] = await objApi.post(`/${model_id[0]}/dashboard/${model}`,body, dispatch);

    if(success)
    dispatch({
        type: SET_DASHBOARD,
        type_form: model,
        obj
    });
    
    // dispatch({ type: SET_SELECTED, model, obj:transfom_to_signed(obj, model) });

} catch (error) {
    console.log(error);
    return false

}
return true

  };


  