import { Grid } from '@mui/material';
import {  useParams } from 'react-router-dom';
import {  DashboardTabEnum, NavigationEnum } from 'types';

import HeaderDashBoard from './header/HeaderDashBoard';
import MainDashboard from './MainDashboard';
import useAuth from 'common/hooks/useAuth';
import HocNavigate from 'common/hooks/useNavigate';
import ReportTableMain from 'views/secunity-ui/Table/table_report/TableBase';
import { tabsOption } from './header/tabs_options';
       


const Dashboard = () => {
    const params = useParams();
    const { permissions } = useAuth();
    // const { set_dashbord } = useActions();
    const navigate = HocNavigate();

    const { model_name } = params;
    let model: any = model_name as DashboardTabEnum;
    const tabsOption_filterd = tabsOption.filter((item)=>item.label!== NavigationEnum.DEVICES_TABLE || permissions.has_client_flowspec_device)

    if (model === undefined  ) {
        const val = parseInt(localStorage.getItem('dashboard_tab') || '0', 10);
        model = tabsOption[val].label
       
        navigate.request_navigate(`dashboard/${model}`)
    }
 
    return (
        <Grid container  justifyContent='center' spacing={0} lg={12}>
            <Grid container spacing={0} mt={-3} lg={12}>
            <HeaderDashBoard tabsOption={tabsOption_filterd}/>
            </Grid>
            {model!==NavigationEnum.REPORTS ? <MainDashboard model_name={model}/>: <ReportTableMain/>}
        </Grid>
    );
};

export default Dashboard;
