import * as React from 'react';

import { TablePagination } from '@mui/material';
import { useActions } from 'store/actions';
import { DefaultRootStateProps } from 'types';
// import { array_objs_array_value, pydict } from 'utils';
import {  useSelector } from 'react-redux';
// import config from 'config';
import useApi from 'common/hooks/useApi';
import HocNavigate from 'common/hooks/useNavigate';

interface Pagination {
    rows: number;
    rowsPerPage: any;
    pagination_model: boolean;
    model: string;
}
const TablePagination1 = ({ rows,   rowsPerPage, pagination_model, model }: Pagination) => {
    const { set_dashbord } = useActions();
    const navigate = HocNavigate();

    const { model_name } = navigate.params;
    const context = useSelector((state: DefaultRootStateProps) => state.dashboard[model_name as 'networks']);
    const get_page = ()=>{
        const pages = (navigate.params.page ? navigate.params.page.split('-') : [1, 1]).map((num:any)=>parseInt(num,10));
        return (model_name=== model|| model.includes(model_name as string)?pages[0]:pages[1]) - 1
    }

    
    const getPostsApi =   useApi(set_dashbord)
 
    const handleChangePage = async(event: React.MouseEvent<HTMLButtonElement, MouseEvent> | null, newPage: number) => {
        
        const [body, pages] = navigate.get_body_dashboard(newPage+1, model, context)
               
       const ans = await getPostsApi.request(model_name as string, body);
       
       if(ans){

           navigate.request_navigate(`dashboard/${model_name}/p/${pages}`)
        }
    };

 

    const rows_per_page = pagination_model
        ? {
              rowsPerPageOptions: [5, 15, 25],
              component: 'div'
          }
        : {
              rowsPerPageOptions: []
          };

    return (
        <TablePagination
            // rowsPerPageOptions={[5, 10, 25]}
            // component="div"
            {...rows_per_page}
            count={rows}
            rowsPerPage={rowsPerPage}
            page={get_page()}
            onPageChange={handleChangePage}
            // onRowsPerPageChange={handleChangeRowsPerPage}
        />
    );
};

export default TablePagination1;
