import { IconButton, TableCell, TableRow, Grid, Typography } from '@mui/material';
import { matrieal_icons } from 'common/menu-items/application';
import { NavigationEnum } from 'types';
import { objApi, pydict } from 'utils';
import { FormattedMessage } from 'react-intl';
import fileDownload from 'js-file-download';
import { useDispatch } from 'react-redux';

interface PropsRowTable {
    cells: string[];
    model: NavigationEnum;
    row_obj: any;
    index: number;
}
const RowTable = ({ cells, model, row_obj, index }: PropsRowTable) => {
    const dispatch = useDispatch();
    
    const handleDownload = async (download_url: string, filename: string) => {
        const ans = await objApi.get(`${download_url}`, dispatch, {
            responseType: 'blob'
        });

        fileDownload(ans[1], `${filename}.pdf`);
    };
    return (
        <TableRow hover role="checkbox" aria-checked tabIndex={-1} key={index}>
            {cells.map((cell: string) => {
                return (
                    <TableCell sx={{ p: 0 }} align="center">
                        <Grid sx={{ display: 'flex', direction: 'row', justifyContent: 'space-evenly' }}>
                            {!['download_url'].includes(cell) ? (
                                !['report_type'].includes(cell) ? (
                                    <Typography minWidth={20} alignContent="center">
                                        {pydict(row_obj, cell, ' ')}
                                    </Typography>
                                ) : (
                                    <FormattedMessage id={pydict(row_obj, cell, ' ')} />
                                )
                            ) : (
                                <IconButton
                                    color="primary"
                                    size="large"
                                    onClick={() => {
                                        handleDownload(row_obj[cell], row_obj['account']);
                                    }}
                                >
                                    <matrieal_icons.FileDownloadIcon sx={{ fontSize: '1.3rem' }} />
                                </IconButton>
                            )}
                        </Grid>
                    </TableCell>
                );
            })}
        </TableRow>
    );
};

export default RowTable;
