import { useState, useEffect,useRef} from 'react';
import ReactApexChart, { Props as ChartProps } from 'react-apexcharts';
import { create_min_max_for_chart, object_equals,  } from 'utils';
import { lineChartOptions } from './style_chart';

type UiInput = {
    series_data: any;
    xaxis_categories: any;
    bps?: boolean;
    indexs_to_show:number[]
};

function ApexLineChart({  series_data, xaxis_categories, bps,indexs_to_show }: UiInput) {

 
    const [series_state, setSeries] = useState<any>([]);
    const [bool_series, set_bool_series] = useState<any>(Array(series_data.length).fill(true));
    const savedCallback: any = useRef();
    useEffect(() => {
        const { seris_to_set, min_max } = create_min_max_for_chart(series_data, bool_series, bps);
        // if(JSON.stringify(seris_to_set)!== JSON.stringify(series_state))
        if(!object_equals(seris_to_set, series_state))
        {
            console.log('dsf');
            
            // setSeries(seris_to_set);
        }
            if(JSON.stringify(min_max)!== JSON.stringify({min: options.yaxis.min, max:options.yaxis.max})){
        setOptions((prevState) => ({
            ...prevState,
         
            yaxis: {
              ...prevState.yaxis,
                ...min_max,
                labels: {
                    ...prevState.yaxis.labels,
                    formatter: (val: string) => {
                        
                        return `${val} ${seris_to_set[bool_series.indexOf(true)>-1 ?bool_series.indexOf(true):0].text}`;
                    }
                }
  
            },

        }));
    }
    }, [  bool_series]);
    const handle_legendClick = (seriesIndex:number )=>{
        const temp_bool = [...bool_series]
        temp_bool[seriesIndex] = !temp_bool[seriesIndex]
        
        set_bool_series(temp_bool)
    }
    useEffect(() => {
        savedCallback.current = handle_legendClick;
    }, [handle_legendClick]);
 
    const [options, setOptions] = useState<ChartProps>({...lineChartOptions,
        chart: {
            ...lineChartOptions.chart,
              events: {
                  legendClick: (chartContext:any, seriesIndex:number )=> {
                    savedCallback.current(seriesIndex)
                  }},   
      
          },
    });

    

    useEffect(() => {
        

        
        const { seris_to_set, min_max } = create_min_max_for_chart(series_data, bool_series, bps);
        if(!object_equals(seris_to_set, series_state))
        setSeries(seris_to_set);
        if(JSON.stringify(min_max)!== JSON.stringify({min: options.yaxis.min, max:options.yaxis.max}))
        setOptions((prevState) => ({
            ...prevState,
        
            xaxis: {
              ...prevState.xaxis,
                categories: xaxis_categories,
            
            },
            tooltip: {
                shared: false,
                y: {
                    title: {
                        formatter: (val: any, { series, seriesIndex, dataPointIndex, w }: any) => {
                            
                            return val.toLowerCase().includes('traffic') && !indexs_to_show.includes( dataPointIndex)? '' : val;
                        }
                    },
                    formatter: (val: any, { series, seriesIndex, dataPointIndex, w }: any) => {
                       
                        return w.globals.seriesNames[seriesIndex].toLowerCase().includes('traffic') && !indexs_to_show.includes( dataPointIndex) ? '' : val;
                    }
                },
        
                x: {
                    format: 'HH:mm'
                },
                theme: 'light'
            },
            yaxis: {
              ...prevState.yaxis,
                ...min_max,
                labels: {
                    ...prevState.yaxis.labels,
                    formatter: (val: string) => {
                        
                        return `${val} ${seris_to_set[bool_series.indexOf(true)>-1 ?bool_series.indexOf(true):0].text}`;
                    }
                }
            }
        }));
    }, [series_data]);
    return (
        <div id="chart">
            <ReactApexChart options={options} series={series_state} type="line" height={350} />
        </div>
    );
}

export default ApexLineChart;
