import { Grid } from '@mui/material';
import React from 'react';
import {  useSelector } from 'react-redux';
import { DefaultRootStateProps, DashboardTabEnum, Model } from 'types';
import useApi from 'common/hooks/useApi';

import ManageDash from './Default';
import Info from './Info';
import { useActions } from 'store/actions';
import HocNavigate from 'common/hooks/useNavigate';
import { pydict } from 'utils';


const MainDashboard = ({ model_name }: Model) => {
    const { set_dashbord } = useActions();
    const navigate = HocNavigate();

    const model: DashboardTabEnum = model_name as DashboardTabEnum;
    
    const dashboard_state = pydict(useSelector((state: DefaultRootStateProps) => state.dashboard), model,{});



    const savedCallback: any = React.useRef();

     
    const getPostsApi =   useApi(set_dashbord)

    const update_dashboard = () => {

        const body = navigate.get_body_dashboard(-1, model, dashboard_state)

        getPostsApi.request(model as string, body[0])

    };
    React.useEffect(() => {
        savedCallback.current = update_dashboard;
    }, [update_dashboard]);

    React.useEffect(() => {
 
        
        update_dashboard()
        const timer = setInterval(
            () => savedCallback.current(),
            6000 * 222
            // 60*1000
        );
        return () => clearInterval(timer);
    }, [model_name]);

 

    return (
 <>
              <Info model_name={model} />
            <Grid container xs={12}>
           <ManageDash model_name={model} />
            </Grid></> 
    );
};

export default MainDashboard;
