import { styled } from '@mui/material/styles';
import { Box, List, ListItem, ListItemText, Typography , Grid} from '@mui/material';

// project imports
import MainCard from 'ui-component/cards/MainCard';
import ReportIcon from '@mui/icons-material/Report';
// import ReportOffIcon from '@mui/icons-material/ReportOff';
import GppGoodIcon from '@mui/icons-material/GppGood';
// assets
import styles from './mystyle.module.css';
import { FormattedMessage } from 'react-intl';

// interface MainStyleProps {
//     theme: Theme;
//     open: boolean;
// }
const CardWrapper = styled(MainCard)(({ theme, open }) => ({
    backgroundColor: theme.palette.primary.dark,
    color: theme.palette.warning.light,
    height: open?'91px':'80px',
    width:open?'89px':'80px',
    overflow: 'hidden',
    position: 'relative',
    borderRadius:open?'120px':'100px',

    '&:after': {
        content: '""',
        position: 'absolute',
        width: 210,
        height: 210,
        background: `linear-gradient(210.04deg, ${theme.palette.primary[200]} -50.94%, rgba(144, 202, 249, 0) 83.49%)`,
        borderRadius: '50%',
        top: -30,
        right: -180
    },
    '&:before': {
        content: '""',
        position: 'absolute',
        width: 210,
        height: 210,
        background: `linear-gradient(140.9deg, ${theme.palette.primary[200]} -14.02%, rgba(144, 202, 249, 0) 77.58%)`,
        borderRadius: '50%',
        top: -160,
        right: -130
    },
    '&:hover .my__unique__button__class-asdf123': {
        //     color: theme.palette.warning.light,
        //     cursor: 'pointer',
        // width: '180px',
        display:'inline'
    
        }
}));

// ==============================|| DASHBOARD - TOTAL INCOME DARK CARD ||============================== //

export interface CardInfoProps {
    primary_text: string;
    is_attack_or_info:boolean;
    secondary_text?: string;

}

const CardInfo = ({ primary_text ,is_attack_or_info, secondary_text}: CardInfoProps) => {
    const under_attack = primary_text === ' under attack';
    let marginLeft_size;
    switch(primary_text.length){
        case 1:
            marginLeft_size= 1
            break;
        case 2:
            marginLeft_size=0.2
            break
        case 3:
            marginLeft_size=-0.1
            break
        case 4:
        case 5:
            marginLeft_size=-0.5
            break
        default:
            marginLeft_size=-1.7
    }
    
    return (
        <>
            <CardWrapper border={false} open={is_attack_or_info} sx={{ backgroundColor:is_attack_or_info? under_attack ? 'error.dark' : 'secondary.dark' : 'primary.dark'}} content={false}>
              { is_attack_or_info? <Box sx={{ pt: 2, pl: 3 }}>
                    <List sx={{ py: 0 }}>
                        <ListItem alignItems="center" disableGutters sx={{ py: 0 }}>
                            <div className={under_attack ? 'no' : styles.close}>
                                <ListItemText
                                    sx={{
                                        py: 0,
                                        mt: 0.45,
                                        mb: 0.45,
                                        zIndex: 1
                                    }}
                                    primary={
                                        under_attack ? (
                                            <Typography variant="h4" sx={{ color: 'warning.dark', ml: -1 }}>
                               <FormattedMessage id="under_attack_circle" />
                                            </Typography>
                                        ) : (
                                            <Typography
                                                variant="h5"
                                                sx={{
                                                    color: 'darkSecondary200',
                                                    ml: -2,
                                                    mt: 0.5,
                                                    fontWeight: 4,
                                                    fontFamily: `'Roboto', sans-serif`
                                                }}
                                            >
                               <FormattedMessage id="notunder_attack_circle" />

                                            </Typography>
                                        )
                                    }
                                    secondary={
                                        under_attack ? (
                                            <ReportIcon sx={{ color: 'warning.dark', ml: 1, flexGrow: 1 }} />
                                        ) : (
                                            <GppGoodIcon sx={{ color: 'secondary.light', ml: 1, flexGrow: 1 }} />
                                        )
                                    }
                                />
                            </div>
                        </ListItem>
                    </List>
                </Box>
          :
          
          
          <Box sx={{ pt: 3, pl: 3 }}>
          <List sx={{ py: 0 }}>
              <ListItem alignItems="center" disableGutters sx={{ py: 0, width: 1.0 }}>
                  <ListItemText
                      sx={{
                          py: 0,
                          mt: 0.45,
                          mb: 0.45,
                          mr: 5
                      }}
                      primary={
                          <Grid display='flex' direction='column'>

                          <Typography
                          className="my__unique__button__class-asdf123"
                          display='none'
                              variant="h4"
                              sx={{
                                  zIndex:2010,
                                  color: '#fff',

                                  // textDecorationColor:'red',
                                  mt:'-0.9rem',
                                  ml: '-.41rem',
                                  textAlign: 'center',
                                  alignItems: 'center',
                                  alignContent: 'center'
                              }}
                          >
                             TOTAL
                          </Typography>
                          <Typography
                              variant="h4"
                              sx={{ color: '#fff', 
                              textAlign: 'center', alignItems: 'center', alignContent: 'center',
                               ml: marginLeft_size 
                              }}
                          >
                              {primary_text}
                              {/* 113 */}
                          </Typography>
                          </Grid>
                           }
                      secondary={

                          <Typography
                              variant="h4"
                              sx={{
                              direction:'row',
                                  color: '#fff',
                                  left: '22%',
                                  textAlign: 'center',
                                  alignItems: 'center',
                                  alignContent: 'center'
                              }}
                          >
                              {secondary_text}
                          </Typography>
                        

                      }
                  />
              </ListItem>
          </List>
      </Box>
          
          }</CardWrapper>
        </>
    );
};

export default CardInfo;
