// material-ui
import { Checkbox, Table, ListItemIcon, TableBody, TableCell, TableContainer, TableRow, Grid } from '@mui/material';
// import TablePagination1 from './TablePagination';
import TablePagination1 from '../TablePaginationDash';
import { useIntl } from 'react-intl';
import {
    IconButton,

} from '@mui/material';
import InfoIcon from '@mui/icons-material/Info';
import HocNavigate from 'common/hooks/useNavigate';

import ReportIcon from '@mui/icons-material/Report';
import ArrowCircleUp from '@mui/icons-material/ArrowCircleUp';
import CancelIcon from '@mui/icons-material/Cancel';
import SubCard from 'ui-component/cards/SubCard';
// import { EnhancedTableHead } from './TableHeader';
import { intersection } from 'lodash';

// assets
import { DefaultRootStateProps, HeadCell, Language, NavigationEnum } from 'types';
import { stableSort } from 'utils';
import { ADD_SIGNED1, REMOVE_SIGNED1 } from 'store/state/type_actions';
import config from 'config';
import style from './style_table_dash';
import { useDispatch, useSelector } from 'react-redux';

export interface TableDashProps {
    cells: string[];
    model_name: NavigationEnum;
    rows: any[];
    headCells: HeadCell[];
    selected: string[];
    handleSelectAllClick?: any;
    handleClick?: any;
    checkbox: boolean;
    colors_map?: any;
    signed_ids?: any;
    signed_key?: NavigationEnum;
    model?: string;
    total?: number;
    func_add?: any;
}
const TableList = ({
    cells,
    rows,
    headCells,
    model_name,
    selected,
    handleSelectAllClick,
    handleClick,
    checkbox,
    colors_map,
    signed_ids,
    signed_key,
    model,
    total,
    func_add
}: TableDashProps) => {
    const dispatch = useDispatch();

    // console.log(colors_map);
    const navigate = HocNavigate();

    const row_to_show = rows.length > 0 && rows[0]['compere'] !== undefined ? stableSort(rows) : rows;
    if(selected===undefined)
    selected=[]
    const containsAllCompleted =  rows.every((c) => selected.find((x) => x === c.id));
    const containsPart = rows.some((c) => selected.find((x) => x === c.id));

    const isSelected = (name: string) => selected.indexOf(name) !== -1;
    const emptyRows = rows.length === 0 ? 5 : 0;
    const context = useSelector((state: DefaultRootStateProps) => state.context[model as string]);
    const pinned = useIntl().formatMessage({ id: Language.PINNED });
    
    return (
        <SubCard
            title={
                <>
                       {checkbox && <Checkbox
                            {...style.checkbox_header}
                            indeterminate={selected.length > 0 && containsPart && !containsAllCompleted}
                            checked={rows.length > 0 && containsAllCompleted}
                            onChange={(e: React.ChangeEvent<HTMLInputElement>)=>
                                {handleSelectAllClick(e.target.checked, signed_key, signed_ids===undefined)}}
                        />}
                    {`${signed_ids===undefined? pinned:''} ${model_name?.toLocaleUpperCase()} (${
                        checkbox
                            ? intersection(
                                  selected,
                                  rows.map((r: any) => r['id'])
                              ).length
                            : rows.length
                    } total)`}
                </>
            }
            {...style.header}
        >
            {/* table */}
            <TableContainer>
                <Table {...style.table_main}>
                    <TableBody>
                        {row_to_show.map((row: any, index: number) => {
                            if (typeof row === 'number') return null;
                            const isItemSelected = isSelected(row['id']);
                            const labelId = `enhanced-table-checkbox-${index}`;
                            let show_add = false;
                            try {
                                show_add = signed_ids && !signed_ids.has(row['id']);
                            } catch (error) {
                                show_add = false;
                            }

                            const model_under_attack =
                                (model !== NavigationEnum.ATTACKS_TABLE &&
                                    row[NavigationEnum.ATTACKS_TABLE] &&
                                    [NavigationEnum.NETWORKS_TABLE, NavigationEnum.DEVICES_TABLE].includes(model_name)) ||
                                (model === NavigationEnum.ATTACKS_TABLE &&
                                    [NavigationEnum.NETWORKS_TABLE].includes(model_name) &&
                                    row[NavigationEnum.ATTACKS_TABLE] &&
                                    context.attacks.some((att_id: string) => row[NavigationEnum.ATTACKS_TABLE].includes(att_id)));
                            return (
                                <TableRow
                                    hover
                                    aria-checked={isItemSelected}
                                    {...style.table_row}
                                    tabIndex={-1}
                                    key={index}
                                    selected={isItemSelected}
                                >
                                    {checkbox && (
                                        <TableCell {...style.table_cell_icons}>
                                            <Grid {...style.table_grid_icons}>
                                                {signed_key && (
                                                    <ListItemIcon
                                                        sx={{
                                                            ml: -1.2,
                                                            mt: [NavigationEnum.ATTACKS_TABLE, NavigationEnum.HOLISTIC].includes(model_name) ? 1.5 : 0,
                                                            mr: -0.2
                                                        }}
                                                    >
                                                        {show_add ? (
                                                            <ArrowCircleUp
                                                                onClick={() => {
                                                                    
                                                                    dispatch({
                                                                        type: ADD_SIGNED1,
                                                                        model,
                                                                        signed_key,
                                                                        obj: row
                                                                    });
                                                                    if (
                                                                        ![
                                                                            NavigationEnum.ATTACKS_TABLE,
                                                                            NavigationEnum.HOLISTIC
                                                                        ].includes(signed_key) &&
                                                                        model !== NavigationEnum.ATTACKS_TABLE
                                                                    ) {
                                                                        func_add(row);
                                                                    }
                                                                }}
                                                                sx={{ ...style.icon_sx, color: 'primary.dark' }}
                                                            />
                                                        ) : (
                                                            !signed_ids && (
                                                                <CancelIcon
                                                                    onClick={() => {
                                                                        dispatch({
                                                                            type: REMOVE_SIGNED1,
                                                                            model,
                                                                            signed_key,
                                                                            id: row['id']
                                                                        });
                                                                    }}
                                                                    sx={{
                                                                        ...style.icon_sx,

                                                                        color: 'error.dark'
                                                                    }}
                                                                />
                                                            )
                                                        )}
                                                    </ListItemIcon>
                                                )}
                                                <Checkbox
                                                    onClick={(event) => handleClick(row['id'],  signed_key, signed_ids===undefined)}
                                                    {...style.checkbox_cell}
                                                    checked={isItemSelected}
                                                    inputProps={{
                                                        'aria-labelledby': labelId
                                                    }}
                                                />

                                                {[NavigationEnum.ATTACKS_TABLE, NavigationEnum.HOLISTIC].includes(model_name) && 
                                                            <IconButton color="primary" size="large" sx={{ml:-1.1,mr:-1.3}} 
                                                            onClick={() => {navigate.request_navigate(`dashboard/${navigate.params.model_name}/${row['id']}`) }} >
                                                            <InfoIcon sx={{ fontSize: '1.3rem' }} />
                                                        </IconButton>
                                                // <ToolTip attack_id={row['id']} />
                                                }
                                            </Grid>
                                        </TableCell>
                                    )}

                                    {[NavigationEnum.NETWORKS_TABLE, NavigationEnum.DEVICES_TABLE].includes(
                                        model_name as NavigationEnum
                                    ) && (
                                        <TableCell {...style.cell_report_icon}>
                                            <ListItemIcon {...style.list_report_icon}>
                                                {model_under_attack && <ReportIcon {...style.item_report_icon} />}
                                            </ListItemIcon>
                                        </TableCell>
                                    )}

                                    {cells.map((cell: string) => (
                                        <TableCell {...style.item_table_cell}>
                                            {cell === 'ip_m' ? `${row['ip']}/${row['mask']}` : row[cell]}
                                        </TableCell>
                                    ))}
                                </TableRow>
                            );
                        })}
                        {emptyRows > 0 && (
                            <TableRow
                                style={{
                                    height: 53 * emptyRows
                                }}
                            >
                                <TableCell colSpan={6} />
                            </TableRow>
                        )}
                    </TableBody>
                </Table>
            </TableContainer>

            {signed_ids && (config.page_dash < rows.length || (total && config.page_dash < total)) ? (
                <Grid mt={-1.5} mb={-2.5}>
                    <TablePagination1
                        model={model_name}
                        pagination_model={false}
                        rows={total || rows.length}
                        rowsPerPage={config.page_dash}
                    />
                </Grid>
            ) : null}
        </SubCard>
    );
};

export default TableList;
