import React from 'react';
 
import {
    Grid, Tab, Tabs,
} from '@mui/material';

import SubCard from 'ui-component/cards/SubCard';



import HocNavigate from 'common/hooks/useNavigate';
import style from './style_header_dashborad';


function a11yProps(index: number) {
    return {
        id: `simple-tab-${index}`,
        'aria-controls': `simple-tabpanel-${index}`
    };
}





 

const HeaderDashBoard = ({tabsOption}:any) => {
 
    const navigate = HocNavigate();
    const { model_name,id } = navigate.params
    const val =  localStorage.getItem('dashboard_tab') || '0'
    const [value, setValue] = React.useState(parseInt(val, 10) );

    const handleChange = (newValue: number) => {
        localStorage.setItem('dashboard_tab', newValue.toString())
        setValue(newValue);
        navigate.request_navigate(`dashboard/${tabsOption[newValue].label}`)
 

    };
    React.useEffect(() => {
        id!==undefined &&setValue(parseInt(localStorage.getItem('dashboard_tab') || '0', 10))

    }, [model_name]);

 
    return (
        <SubCard {...style.tabs_header_dashboard_card}>
         
            <Grid {...style.tabs_header_dashboard_grid} >
                <Tabs
                    value={value}
                    onChange={(e, newValue) => handleChange(newValue)}
                    {...style.tabs}>
                    
                    {tabsOption.map((tab:any, index:number) => (
                        <Tab  href={`${navigate.get_prefix_to_navigate( )}dashboard/${tab.label}`} 
                        onClick={event => {
                            event.preventDefault();
                          }}
                        key={index} {...tab} {...style.tab} {...a11yProps(index)} />
                    ))}
                </Tabs>
            </Grid>
        </SubCard>
    );
};

export default HeaderDashBoard;